<template>
  <el-row>
    <el-col :span="6" class="colphoto" v-for="(item, index) in photos" :key="index">
      <el-card :body-style="{ padding:'10px'}">
        <img src="/static/images/hamburger.png" class="image">
          <div>
            <time class="time">{{ item.currentDate }}</time>
            <el-button type="text" class="button">操作按钮1</el-button>
            <el-button type="text" class="button">操作按钮2</el-button>
          </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'photo',
  data() {
    return {
      photos: [{
        id: 1,
        currentDate: new Date()
      }, {
        id: 2,
        currentDate: new Date()
      }, {
        id: 3,
        currentDate: new Date()
      }, {
        id: 4,
        currentDate: new Date()
      }, {
        id: 5,
        currentDate: new Date()
      }],
      
    };
  }
}
</script>
<style>
.time {
  font-size: 11px;
  color: #999;
}

.button {
  padding: 0;
}

.image {
  width: 100%;
  display: block;
}
.colphoto {
  padding: 10px;
}
</style>


